.footer {
  padding-top: 25px;
  padding-bottom: 55px;

  @include mobile {
    padding-bottom: 20px;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 920px) {
      flex-direction: column;
    }

    @include mobile {
      align-items: start;
    }
  }

  &__logo {
    margin-bottom: 20px;

    @include mobile {
      display: none;
    }
  }
}

.nav {
  @media (max-width: 920px) {
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    align-items: center;

    @include mobile {
      flex-direction: column;
      align-items: start;
    }
  }

  &__item:not(:last-child) {
    margin-right: 32px;

    @include small-tablet {
      margin-right: 25px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: var(--light-color);
    transition: color 0.3s ease-in;

    @include small-tablet {
      font-size: 13px;
      line-height: 16px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 17px;
    }

    &:hover {
      color: var(--accent-color);
    }
  }
}

.social {
  &__icons {
    display: flex;
    align-items: center;
  }

  &__item .social__link svg {
    @include mobile {
      width: 34px;
      height: 34px;
    }
  }

  &__item:not(:last-child) {
    margin-right: 10px;

    @include mobile {
      margin-right: 15px;
    }
  }

  &__link svg path {
    fill: var(--light-color);
    transition: fill 0.3s ease-in;
  }

  &__link--tg:hover svg path {
    fill: #039BE5;
  }

  &__link--vk:hover svg path {
    fill: #0077FF;
  }
}
