.cta {
  position: relative;
  padding-top: 300px;
  padding-bottom: 250px;

  @include mobile {
    padding-top: 100px;
    padding-bottom: 60px;
  }

  &::before {
    position: absolute;
    content: "";
    left: 70px;
    bottom: 10px;
    width: 357px;
    height: 350px;
    background-image: url('../img/bg/bg-3.png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;

    @include tablet {
      left: 0px;
      width: 328px;
      height: 328px;
    }
    @include small-tablet {
      left: -30px;
      width: 282px;
      height: 288px;
    }
    @include mobile {
      display: none;
    }
  }

  &::after {
    position: absolute;
    content: "";
    right: -150px;
    bottom: -40px;
    width: 625px;
    height: 537px;
    background-image: url('../img/bg/bg-4.png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;

    @include tablet {
      bottom: -30px;
      width: 515px;
      height: 446px;
    }
    @include small-tablet {
      right: -100px;
      bottom: -30px;
      width: 395px;
      height: 352px;
    }
    @include mobile {
      right: -60px;
      bottom: -160px;
      width: 290px;
      height: 278px;
      background-image: url('../img/bg/bg-3-small.png');
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include mobile {
      margin-bottom: 25px;
    }
  }
}

.form {
  margin-right: auto;
  margin-left: auto;
  width: 390px;

  @media (max-width: 460px) {
    width: 100%;
  }

  &__label {
    width: 100%;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__label--checkbox {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px;

    @include mobile {
      margin-top: 13px;
      margin-bottom: 13px;
    }
  }

  &__input {
    width: 100%;
    color: #ADADAD;
    font-weight: 600;
    font-size: 13px;
    line-height: 15px;
    padding: 16px 18px;
    border-radius: 6px;
    border: 1px solid #86858A;
    background-color: transparent;

    @media (max-width: 460px) {
      font-size: 12px;
      line-height: 14px;
      padding: 13px 10px 12px 10px;
    }
  }

  &__label {
    position: relative;
    width: 100%;

    &--steam::before {
      position: absolute;
      content: "";
      top: 0px;
      right: 12px;
      width: 18px;
      height: 18px;
      background-image: url('../img/svg/steam.svg');
      background-repeat: no-repeat;
      background-size: contain;

      @media (max-width: 460px) {
        top: 2px;
        right: 10px;
        width: 16px;
        height: 16px;
      }
    }
    &--ruble::before {
      position: absolute;
      content: "";
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
      width: 18px;
      height: 18px;
      background-image: url('../img/svg/ruble.svg');
      background-repeat: no-repeat;
      background-size: contain;

      @media (max-width: 460px) {
        right: 10px;
        width: 16px;
        height: 16px;
      }
    }

    &--promo {
      margin-left: 6px;
      width: 130px;

      @media (max-width: 460px) {
        width: 100px;
      }
    }
  }

  &__input--promo {
    width: 130px;

    @media (max-width: 460px) {
      width: 100px;
    }
  }

  .input__text {
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--light-color);
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;

    @media (max-width: 460px) {
      font-size: 12px;
      line-height: 14px;
    }

    span {
      font-weight: 600;
    }
  }
}

.form__input-large--ruble {
  display: inline-block;
}

.form__input-small--ruble {
  display: none;
}

@media (max-width: 460px) {
  .form__input-large--ruble {
    display: none;
  }

  .form__input-small--ruble {
    display: inline-block;
  }
}

.form_radio_group {
  margin-top: 10px;
  display: inline-flex;
  justify-content: space-between;
  overflow: hidden;
}

.form_radio_group-item {
  flex: 0 1 25%;
}

.form_radio_group input[type=radio] {
  display: none;
}

.form_radio_group label {
  width: 100%;
  display: inline-flex;
  cursor: pointer;
  font-size: 14px;
  line-height: 47px;
  border: 1px solid #86858A;
  border-right: none;
  user-select: none;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: #fff;
  transition: color 0.3s ease-in;

  @media (max-width: 460px) {
    font-size: 13px;
    line-height: 14px;
    padding: 13px 10px 12px 10px;
  }
}

.form_radio_group .form_radio_group-item:first-child label {
  border-radius: 6px 0 0 6px;
}

.form_radio_group .form_radio_group-item:last-child label {
  border-radius: 0 6px 6px 0;
  border-right: 1px solid #86858A;
}

/* Hover */
.form_radio_group label:hover {
  color: var(--accent-color);
}

/* Checked */
.form_radio_group input[type=radio]:checked+label {
  color: #fff !important;
  background: var(--accent-color);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.checkbox__text {
  font-size: 12px;
  line-height: 16px;
  color: var(--light-color);
}

.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.custom-checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.custom-checkbox+label::before {
  content: '';
  display: inline-block;
  width: 12px;
  height: 12px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #86858A;
  border-radius: 3px;
  margin-right: 8px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 90% 90%;

  @include mobile {
    width: 16px;
    height: 16px;
  }
}

.custom-checkbox:checked+label::before {
  border-color: #fff;
  background-color: #fff;
  background-image: url("../img/svg/check.svg");
}

.form__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 16px 61px;
  width: 100%;
  font-family: 'Gilroy', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: var(--light-color);
  border-radius: 14px;
  background-color: var(--accent-color);
  transition: background-color 0.3s ease-in;

  @include mobile {
    padding: 12px 40px 13px 40px;
    font-size: 14px;
  }

  &:hover {
    background-color: var(--accent-darkColor);
  }

  &:active {
    background-color: var(--accent-color);
  }
}
