.header {
  position: relative;
  left: 0;
  top: 0;
  padding: 28px 0;
  width: 100%;

  @include mobile {
    position: fixed;
    padding: 20px 0;
    background: rgba(0, 0, 0, 0.15);
    backdrop-filter: blur(60px);
    z-index: 500;
  }
}

.logo {
  font-family: "logofont", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--light-color);

  @include mobile {
    font-size: 16px;
    line-height: 20px;
  }

  span {
    color: var(--red-color);
  }
}
