.hero {
  position: relative;
  padding-top: 50px;

  @include mobile {
    padding-top: 100px;
  }

  &::before {
    position: absolute;
    content: "";
    left: 70px;
    bottom: -350px;
    width: 563px;
    height: 425px;
    background-image: url('../img/bg/bg-1.png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;

    @media (max-width: 1400px) {
      left: 0;
    }
    @include tablet {
      left: -60px;
      bottom: -300px;
      width: 503px;
      height: 387px;
    }
    @include small-tablet {
      left: -120px;
      width: 424px;
      height: 338px;
    }
    @include mobile {
      position: absolute;
      content: "";
      left: -180px;
      bottom: -200px;
      width: 478px;
      height: 387px;
      background-image: url('../img/bg/bg-4-small.png');
      background-repeat: no-repeat;
      background-size: cover;
      z-index: -1;
    }
  }

  &::after {
    position: absolute;
    content: "";
    right: 60px;
    bottom: -450px;
    width: 502px;
    height: 410px;
    background-image: url('../img/bg/bg-2.png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;

    @media (max-width: 1400px) {
      right: -40px;
    }
    @include tablet {
      bottom: -370px;
      width: 460px;
      height: 380px;
    }
    @include small-tablet {
      right: -120px;
    }
    @include mobile {
      display: none;
    }
  }

  &__container {
    display: flex;
    align-items: center;

    @media (max-width: 810px) {
      justify-content: space-between;
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__content {
    margin-top: 50px;
    width: 50%;

    @media (max-width: 810px) {
      width: 400px;
    }

    @include small-tablet {
      margin-top: 0;
      margin-bottom: 50px;
      width: 100%;
      text-align: center;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
    font-family: "Gilroy", sans-serif;
    font-weight: 500;
    font-size: 46px;
    line-height: 105%;
    color: var(--light-color);

    @include mobile {
      margin-bottom: 15px;
      font-size: 34px;
      line-height: 110%;
    }

    span {
      color: var(--accent-color);
    }
  }

  &__descr {
    margin-bottom: 18px;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.03em;
    color: var(--light-color);

    @include small-tablet {
      margin-bottom: 25px;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 16px;
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 16px 61px;
    min-width: 240px;
    font-family: 'Gilroy', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);
    border-radius: 14px;
    background-color: var(--accent-color);
    transition: background-color 0.3s ease-in;

    @include mobile {
      padding: 12px 40px 13px 40px;
      font-size: 14px;
      min-width: 200px;
    }

    &:hover {
      background-color: var(--accent-darkColor);
    }

    &:active {
      background-color: var(--accent-color);
    }
  }

  &__requirements {
    width: 50%;

    @media (max-width: 810px) {
      width: 300px;
    }

    @include small-tablet {
      width: 100%;
    }

    &-title {
      margin: 0;
      padding: 0;
      max-width: 280px;
      margin-right: auto;
      margin-bottom: 37px;
      margin-left: auto;
      font-weight: 400;
      font-size: 22px;
      line-height: 120%;
      text-align: center;
      color: var(--light-color);

      @include small-tablet {
        max-width: 100%;
      }

      @include mobile {
        margin-bottom: 25px;
        font-size: 18px;
      }
    }

    &-cards {
      display: grid;
      justify-content: center;
      grid-template-columns: repeat(2, 134px);
      row-gap: 20px;
      column-gap: 32px;

      @media (max-width: 810px) {
        column-gap: 20px;
      }
    }

    &-card {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 24px 10px 20px 10px;
      border: 3px solid #7668CA;
      border-radius: 15px;

      @include mobile {
        background: rgba(0, 0, 0, 0.15);
        backdrop-filter: blur(60px);
      }
    }

    &-img {
      width: 40px;
      height: 40px;
      margin-bottom: 12px;

      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    &-img--fluid {
      width: 52px;
      height: 40px;
      margin-bottom: 12px;

      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      text-align: center;
      color: var(--light-color);
    }
  }
}
